;IACE,0BAAA;E/DyxMF;AACF;AA38MA;E+DqLE,wBAAA;A/DyxMF;A+DvxME;EAAA;IACE,gCAAA;E/D0xMF;AACF;A+DtxME;EAAA;ICjLA,wBAAA;EhE28MA;AACF;AAp9MA;EACI,YAAA;EACA,YAAA;EACA,kBAAA;EACA,oBAAA;EACA,oBAAA;EACA,kBAAA;AAs9MJ;AAn9MA;EACI,sCAAA;EACA,6EAAA;EACA,kEAAA;EACA,aAAA;AAq9MJ;AAl9MA;EACI,6EAAA;EACA,kEAAA;EACA,kBAAA;AAo9MJ;AAj9MA;EACI,6EAAA;EACA,kEAAA;AAm9MJ;AAh9MA;EACI,iDAAA;AAk9MJ;AA/8MA;EACI,kDAAA;EACA,uCAAA;EACA,YAAA;EACA,sCAAA;EACA,iDAAA;AAi9MJ;AA98MA;EACI,gBAAA;AAg9MJ;AA78MA;EACI,iBAAA;AA+8MJ;AA58MA;EACI,mBAAA;AA88MJ;AA38MA;EACI,iDAAA;EACA,sCAAA;AA68MJ;AA18MA;EACI,kDAAA;EACA,uCAAA;AA48MJ;AAz8MA;EACI,6BAAA;EACA,kEAAA;EACA,kBAAA;AA28MJ;AAx8MA;EACI,oDAAA;EACA,yCAAA;AA08MJ;AAv8MA;EACI,oDAAA;EACA,yCAAA;AAy8MJ;AAt8MA;EACI,oDAAA;EACA,yCAAA;AAw8MJ;AAr8MA;EACI,kBAAA;AAu8MJ;AAj8MA;EACI,6EAAA;EACA,yEAAA;AAm8MJ;AAh8MA;EACI,6EAAA;AAk8MJ;AA/7MA;EACI,6EAAA;EACA,yEAAA;AAi8MJ;AA77MA;EACI,mBAAA;EACA,iBAAA;EACA,yEAAA;AA+7MJ;AA17MA;EACI,qFAAA;AA47MJ;AAz7MA;EACI,qFAAA;AA27MJ;AAx7MA;EACI,qFAAA;AA07MJ;AAt7MA;EACI,kEAAA;EACA,6EAAA;AAw7MJ","sourcesContent":["/* Import bootstrap, but prefix it all with our grid div so we don't clobber our interactive windows styles */\n#variable-explorer-data-grid {\n    @import \"~bootstrap-less/bootstrap/bootstrap.less\";\n}\n\n#variable-explorer-data-grid .form-control {\n    height: auto;\n    padding: 0px;\n    font-size: inherit;\n    font-weight: inherit;\n    line-height: inherit;\n    border-radius: 0px;\n}\n\n#variable-explorer-data-grid .react-grid-Main {\n    font-family: var(--vscode-font-family);\n    background-color: var(--override-background, var(--vscode-editor-background));\n    color: var(--override-foreground, var(--vscode-editor-foreground));\n    outline: none;\n}\n\n#variable-explorer-data-grid .react-grid-Grid {\n    background-color: var(--override-background, var(--vscode-editor-background));\n    color: var(--override-foreground, var(--vscode-editor-foreground));\n    border-style: none;\n}\n\n#variable-explorer-data-grid .react-grid-Canva